/* journal archive home.php*/
.blog {
  .content-area {
    .journal-entry {
      margin-bottom: 3rem;
      .entry-title {
        position: absolute;
        top: 20px;
        left: 0;
        background: #248a83;
        padding: 10px;
        color: white;
        text-transform: uppercase;
        font-size: 2rem;
        & a {
          color: white;
          text-decoration: none;
          &:hover {
            color: #f0e4d1;
          }
        }

        .entry-meta {
          position: absolute;
          bottom: 7px;
          right: 0;
          color: white;
          background: #248a83;
          padding: 5px;
          text-transform: uppercase;
        }
      }
    }
  }
}
